interface TextProps {
    /**
    * What is the type of text?
    */
    type?: "h1" | "h2" | "h3" | "h4" | "p";
    /**
     * What is the content?
     */
    content?: string;
}

export const Text = ({
    type,
    content,
    ...props
}: TextProps) => {
    const selection = () => {
        if (type = "h1") {
            <h1 {...props}>{content}</h1>
        } else if (type = "h2") {
            <h2 {...props}>{content}</h2>
        } else if (type = "h3") {
            <h3 {...props}>{content}</h3>
        } else if (type = "h4") {
            <h4 {...props}>{content}</h4>
        } else {
            <p {...props}>{content}</p>
        }
    };
    return (
        <>
            {selection}
        </>
    );
};
